import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'

import { useTheme } from 'styled-components'
import styles from './trading_chart.module.scss'

import TradingChart from 'components/charts/TradingChart'

import ToggleBtn from 'components/btns/ToggleBtn'

import { authNoKeyApi } from 'lightcone_api/api_wrapper'

import { genOHLC, genOption } from 'utils/kline_tools'

import { useTradingInfo } from 'hooks/trading_hook'

import i18n from 'plugins/i18n'

export interface ChartWrapperProps {
}

const TradingChartPanel = ({ }: ChartWrapperProps) => {

    const theme: any = useTheme()

    const tradingInfo = useTradingInfo()

    const upColor = theme.colors.upColor
    const upBorderColor = theme.colors.upBorderColor
    const downColor = theme.colors.downColor
    const downBorderColor = theme.colors.downBorderColor

    const { t } = useTranslation()

    const defaultOption = genOption([], upColor, upBorderColor, downColor, downBorderColor)

    const getOption = async () => {
        const endTS = new Date().getTime()* 1000
        const data1 = await authNoKeyApi.getCandlestick(tradingInfo.symbol, tradingInfo.interval)
        var data = []
        if (data1.status == 200) {
            data = genOHLC(data1.data['candlesticks'], i18n.language)
        }
        return genOption(data, upColor, upBorderColor, downColor, downBorderColor)
    }

    return (
        <div className='draggable_panel'>
            <div className='draggable_header' >
                {t('pages:chart_title')}
                <div className={styles.toggle_outter} >
                    <ToggleBtn left_txt={t('pages:chart_toggle_left')} right_txt={t('pages:chart_toggle_right')} left_value={'Chart'} right_value={'Depth'} />
                </div>
            </div>
            <TradingChart defaultOption={defaultOption} getOption={getOption} />
        </div>
    );
};

export default TradingChartPanel;
