import { createSlice, PayloadAction } from "@reduxjs/toolkit"

export enum TradingInterval {
  min1 = '1min', 
  min5 = '5min', 
  min15 = '15min', 
  min30 = '30min', 
  hr1 = '1hr', 
  hr2 = '2hr', 
  hr4 = '4hr', 
  hr12 = '12hr', 
  d1 = '1d', 
  w1 = '1w',
}

export interface TradingInfo {
  symbol: string
  symbol_base: string
  symbol_quote: string
  interval: TradingInterval
  markets: any[]
}

const initialState = { symbol: 'LRC-USDT', symbol_base: 'USDT', symbol_quote:'LRC', interval: TradingInterval.hr1, markets: [] } as TradingInfo

const tradingSlice = createSlice({
  name: "trading",
  initialState,
  reducers: {
    changeSymbol(state, action: PayloadAction<any>) {
        state.symbol = action.payload
        const ind = state.symbol.indexOf('-')
        state.symbol_base = state.symbol.substr(ind)
        state.symbol_quote = state.symbol.substr(0, ind)
    },
    changeTradingInterval(state, action: PayloadAction<any>) {
        state.interval = action.payload
    },
    setMarkets(state, action: PayloadAction<any>) {
      console.log('enter setMarkets:', action.payload)
        state.markets = action.payload
    },
  },
})

export const { changeSymbol, changeTradingInterval, setMarkets } = tradingSlice.actions
export default tradingSlice
