import { createSlice, PayloadAction } from "@reduxjs/toolkit"

export interface SettingsState {
  themeType: string
  lng: string
}

const initialState = { themeType: "dark", lng: 'en_US' } as SettingsState

const settingsSlice = createSlice({
  name: "settings",
  initialState,
  reducers: {
    changeTheme(state) {
      if (state.themeType === "dark") {
        state.themeType = "light"
      } else {
        state.themeType = "dark"
      }
    },
    changeLng(state) {
      if (state.lng === "en_US") {
        state.lng = "zh_CN"
      } else {
        state.lng = "en_US"
      }
    },
    setLng(state, action: PayloadAction<string>) {
        state.lng = action.payload
    },
  },
})

export const { changeTheme, changeLng, setLng } = settingsSlice.actions
export default settingsSlice
