import { useEffect, useState } from 'react'
import { useTranslation } from "react-i18next"

import styled from 'styled-components/macro'
import { useTheme } from 'styled-components'
import styles from './order_book.module.scss'

import IconBtn from 'components/btns/IconBtn'
import OrderBookTable from 'components/tables/OrderBookTable'

import { useTradingInfo } from 'hooks/trading_hook'

import { authNoKeyApi } from 'lightcone_api/api_wrapper'
import { DepthData } from 'defs/exchange_defs'

export interface OrderBookPanelProps {
}

const AskLine = styled.div`
color: #00BBA8;
`

const BidLine = styled.div`
color: #FF5677;
`

const OrderBookPanel = ({ }: OrderBookPanelProps) => {

    const theme: any = useTheme()

    const { t } = useTranslation()

    const tradingInfo = useTradingInfo()

    const [depth, setDepth] = useState<DepthData>()

    useEffect(() => {

        const id = setInterval(async () => {
            const depth_data = (await authNoKeyApi.getMarketDepth(tradingInfo.symbol, 0, 20))
            setDepth(depth_data)
            // console.log(depth)
        }, 1000)

        return () => clearInterval(id)
    })

    const onOrderBookTypeChange = (event: any) => {
        console.log('onOrderBookTypeChange')
    }

    return (
        <div className='draggable_panel'>
            <div className='draggable_header' >
                {t('pages:order_book_title')}
            </div>
            <IconBtn marginLeft={'12px'} onClick={onOrderBookTypeChange} src={theme.imgs.obt1_img} w={'15px'} h={'10px'} />
            <IconBtn marginLeft={'18px'} onClick={onOrderBookTypeChange} src={theme.imgs.obt2_img} w={'15px'} h={'10px'} />
            <IconBtn marginLeft={'18px'} onClick={onOrderBookTypeChange} src={theme.imgs.obt3_img} w={'15px'} h={'10px'} />
            <OrderBookTable depth={depth} />
        </div>
    );
};

export default OrderBookPanel
