import globalAxios, { AxiosPromise, AxiosInstance } from "axios"
import { createRequestFunction } from "lightcone_api/api/common"

import { GetApiKeyRequest } from 'lightcone_api/api'

import {
  BASE_PATH,
  COLLECTION_FORMATS,
  RequestArgs,
  BaseAPI,
  RequiredError,
} from "lightcone_api/api/base"

import * as api from "lightcone_api/api"

import { getEdDSASig, getEdDSASigWithPoseidon, getEcDSASig } from "lightcone_api/api_wrapper/sign_tools"

class AuthApiKeyApi {

  localVarAxiosParamCreator: any
  conf: any

  constructor(conf: any) {
    this.conf = conf
    this.localVarAxiosParamCreator = api.AuthApiKeyApiAxiosParamCreator(conf)
  }

  async getNextStorageId(accountId: number, sellTokenId: number, PrivateKey: string) {

    const localVarAxiosArgs = await this.localVarAxiosParamCreator.getNextStorageId(accountId, sellTokenId)

    const request = createRequestFunction(localVarAxiosArgs, globalAxios, BASE_PATH, this.conf)

    const response = await request()

    return response
  }

}

export { AuthApiKeyApi }
