import { useSelector } from "react-redux"

import { RootState } from "stores"

import { AccountStatus } from "stores/reducers/account_reducer"

export function useAccount() {

    const accStatus = useSelector((state: RootState) => state.account.status)

    const isLogined = accStatus === AccountStatus.Logined

    return {
        accStatus: accStatus, 
        isLogined: isLogined
    }

}
